.Range{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:100%;line-height:15px;height:46px;border:1px solid #000;background:#fff;color:#000;border-radius:8px;box-sizing:border-box;cursor:pointer;font-size:13px;font-weight:400}.Range--dark{border:1px solid #fff;background:#0c0c0c;color:#fff}.Range--open{border-bottom:1px solid rgba(0,0,0,0);border-radius:8px 8px 0 0}.Range__title{margin:14px 0 15px 14px}.Range__arrow{position:absolute;display:flex;right:18px;float:right;fill:#b3b3b3}.Range__сontainer{display:block;position:absolute;justify-content:center;align-items:center;align-self:center;width:calc(100% + 2px);top:45px;left:-1px;border:1px solid #000;padding:20px 22px;box-sizing:border-box;border-radius:0 0 8px 8px;background:#fff;z-index:1}.Range__сontainer--dark{border:1px solid #fff;background:#0c0c0c;color:#fff}.Range__сontainer--open{border-top:none;display:flex}